import { instanceMap } from "../utils/map/instance";
import { switchLayer } from "../utils/map/wind";

export default {
  props: {
    disabled: {
      default: true,
    },
  },
  data() {
    return {
      control: false,
    };
  },
  watch: {
    control(value) {
      switchLayer(instanceMap(), value);
    },
  },
};
