import { measurements } from "../utils/measurement";

export default {
  props: ["type"],
  computed: {
    hasIcon() {
      return (
        measurements[this.type.toLowerCase()] &&
        measurements[this.type.toLowerCase()].icon
      );
    },
    icon: function () {
      return measurements[this.type.toLowerCase()].icon;
    },
  },
};
