export default {
  props: ["localeCurrent", "city"],
  data() {
    return {
      locale: this.localeCurrent,
    };
  },
  watch: {
    locale(newValue) {
      this.$i18n.locale = newValue;
      localStorage.setItem("locale", newValue);
    },
  },
};
