import moment from "moment";

export default {
  props: ["current", "canHistory"],
  data() {
    return {
      isIpfs: this.current === "ipfs",
      isConnectionRemote: null,
      start: moment().format("YYYY-MM-DD"),
      maxDate: moment().format("YYYY-MM-DD"),
    };
  },
  computed: {
    startTimestamp: function () {
      return Number(
        moment(this.start + " 00:00:00", "YYYY-MM-DD HH:mm:ss").format("X")
      );
    },
    endTimestamp: function () {
      return Number(
        moment(this.start + " 23:59:59", "YYYY-MM-DD HH:mm:ss").format("X")
      );
    },
  },
  created() {
    if (this.current == "remote") {
      setInterval(() => {
        if (this.$provider) {
          this.isConnectionRemote = this.$provider.connection;
        }
      }, 1000);
    }
  },
  watch: {
    canHistory: {
      handler(newValue) {
        if (newValue) {
          this.getHistory();
        }
      },
      immediate: true,
    },
    start() {
      this.getHistory();
    },
    isIpfs(newValue) {
      if (newValue) {
        this.set("ipfs");
      } else {
        this.set("remote");
      }
    },
  },
  methods: {
    set(type) {
      setTimeout(() => {
        this.$router
          .push({
            name: "main",
            params: {
              provider: type,
              type: this.$route.params.type,
              zoom: this.$route.params.zoom,
              lat: this.$route.params.lat,
              lng: this.$route.params.lng,
            },
          })
          .catch(() => {});
        window.location.reload(false);
      }, 500);
    },

    getHistory() {
      if (this.isIpfs) {
        return;
      }
      this.$emit("history", {
        start: this.startTimestamp,
        end: this.endTimestamp,
      });
    },
  },
};
